.chapter-page {
  height: 100vh;
  background-color: #f5f5f5;
  overflow: hidden;
}

.chapter-container {
  max-width: 800px;
  margin: 0 auto;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 2rem;
}

.chapter-title {
  text-align: center;
  color: #333;
  font-size: 2em;
  margin-bottom: 2rem;
  font-family: 'Georgia', serif;
}

.chapter-content {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 60px;
}

@media (max-width: 768px) {
  .chapter-page {
    padding: 1rem;
  }

  .chapter-container {
    padding: 1rem;
  }

  .chapter-title {
    font-size: 1.5em;
    margin-bottom: 1.5rem;
  }
}
