.book-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.back-cover-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.back-cover {
  width: min(95%, calc(90vh * 0.707));
  height: 90vh;
  position: relative;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  overflow: hidden;
}

.back-cover-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.back-cover-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.back-cover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(0, 0, 0, 0.8) 100%
  );
  color: white;
  overflow: hidden;
}

.back-cover-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  overflow: hidden;
}

.back-cover-title {
  font-size: min(2em, 4vh);
  margin-bottom: min(30px, 4vh);
  font-family: 'Georgia', serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  text-align: center;
}

.back-cover-text {
  font-size: min(1.05em, 2.05vh);
  line-height: 1.3;
  font-family: 'Georgia', serif;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  text-align: justify;
  margin: 0;
  white-space: pre-wrap;
  flex: 1;
  overflow-y: hidden;
}

.back-cover-button {
  padding: 10px 20px;
  font-size: 1.2em;
  background-color: transparent;
  border: 2px solid white;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  align-self: center;
  margin-top: 20px;
}

.back-cover-button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.navigation-buttons {
  position: absolute;
  bottom: 30px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.nav-button {
  padding: 15px 30px;
  font-size: 1.2em;
  background-color: rgba(255, 255, 255, 0.2);
  border: 2px solid white;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.nav-button:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

/* Responsive design */
@media (max-width: 768px) {
  .back-cover-overlay {
    padding: 30px;
  }

  .back-cover-title {
    font-size: min(2.5em, 5vh);
    margin-bottom: min(25px, 3vh);
  }

  .back-cover-text {
    font-size: min(1.05em, 2.05vh);
  }
}

@media (max-width: 480px) {
  .back-cover-overlay {
    padding: 20px;
  }

  .back-cover-title {
    font-size: min(2em, 4vh);
    margin-bottom: min(20px, 2vh);
  }

  .back-cover-text {
    font-size: min(1.05em, 2.05vh);
  }
}
