.characters-container {
  height: 100vh;
  padding: 20px;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  overflow: hidden;
}

/* Style du livre en format A4 */
.characters-grid {
  display: flex;
  position: relative;
  background: white;
  padding: 0;
  margin: 0 auto;
  /* Format A4 en paysage (deux pages A4 côte à côte) */
  width: min(95%, calc(2 * (90vh * 0.707)));
  height: 90vh;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.page-title {
  font-size: min(2.5rem, 4vh);
  color: #2c3e50;
  margin: 10px 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
}

/* Effet de tranche au milieu */
.characters-grid::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0.2) 40%,
    rgba(0, 0, 0, 0.2) 60%,
    rgba(0, 0, 0, 0.1)
  );
  box-shadow: inset -1px 0 3px rgba(0, 0, 0, 0.1),
              inset 1px 0 3px rgba(0, 0, 0, 0.1);
  transform: translateX(-50%);
}

.character-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
}

/* Ombre sur les bords intérieurs */
.character-card:first-child {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.character-card:last-child {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.character-image {
  width: 90%;
  height: 80%;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: auto;
}

.character-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.character-info {
  margin-top: auto;
}

.character-card h2 {
  font-size: min(2rem, 3vh);
  color: #2c3e50;
  margin: 8px 0;
  font-weight: bold;
  text-align: center;
}

.character-description {
  font-size: min(1rem, 2vh);
  color: #666;
  line-height: 1.3;
  text-align: center;
  margin: 0 15px;
}

.navigation-button {
  padding: 10px 20px;
  font-size: min(1rem, 2vh);
  border: none;
  border-radius: 4px;
  background-color: #2c3e50;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  font-weight: 500;
  margin: 10px 0;
}

.navigation-button:hover {
  background-color: #34495e;
  transform: translateY(-2px);
}

/* Responsive design */
@media (max-width: 900px) {
  .characters-grid {
    flex-direction: column;
    height: 85vh;
    width: min(95%, calc(90vh * 0.707));
  }

  .characters-grid::after {
    width: 100%;
    height: 4px;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }

  .character-card {
    padding: 15px;
  }

  .character-image {
    height: 45%;
  }
}

@media (max-width: 480px) {
  .characters-container {
    padding: 10px;
  }

  .page-title {
    font-size: min(1.8rem, 3vh);
  }

  .character-image {
    height: 40%;
  }

  .navigation-button {
    padding: 8px 16px;
  }
}
