.cover-page {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
  z-index: 1000;
  background-color: #f5f5f5;
}

.cover-page-content {
  width: min(95%, calc(90vh * 0.707));
  height: 90vh;
  position: relative;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  overflow: hidden;
  margin: 0 auto;
}

.cover-page-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.cover-page-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cover-page-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(0, 0, 0, 0.8) 100%
  );
  color: white;
  overflow: hidden;
}

.cover-page-text {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  overflow: hidden;
}

.cover-page-title {
  font-size: min(3em, 6vh);
  margin-bottom: min(30px, 4vh);
  font-family: 'Georgia', serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  text-align: center;
  text-transform: uppercase;
  color: white;
}

.cover-page-author {
  font-size: min(2em, 4vh);
  font-family: 'Georgia', serif;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  color: white;
  text-align: center;
}

.cover-page-buttons {
  display: flex;
  gap: 20px;
  margin-top: auto;
  justify-content: center;
  flex-wrap: wrap;
}

.cover-page-button {
  padding: 10px 20px;
  font-size: 1.2em;
  background-color: transparent;
  border: 2px solid white;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cover-page-button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* Responsive design */
@media (max-width: 768px) {
  .cover-page {
    padding: 10px;
  }

  .cover-page-content {
    height: 85vh;
  }

  .cover-page-overlay {
    padding: 30px;
  }

  .cover-page-title {
    font-size: min(2.8rem, 5vh);
    letter-spacing: 2px;
  }

  .cover-page-author {
    font-size: min(1.6rem, 3vh);
  }

  .cover-page-button {
    padding: 8px 16px;
    font-size: min(0.9rem, 1.8vh);
    min-width: 120px;
  }
}

@media (max-width: 480px) {
  .cover-page-overlay {
    padding: 20px;
  }

  .cover-page-title {
    font-size: min(2rem, 4vh);
    letter-spacing: 2px;
  }

  .cover-page-author {
    font-size: min(1.2rem, 2.5vh);
  }

  .cover-page-buttons {
    flex-direction: column;
    gap: 10px;
  }

  .cover-page-button {
    width: 100%;
    max-width: 200px;
  }
}

/* Animation pour la transition des pages */
.page-transition {
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
